<template>
  <el-dialog
    title="重置密码"
    :visible="visible"
    :before-close="handleClose"
    width="500px"
  >
    <el-form ref="form" label-width="70px">
      <el-form-item label="邮箱">
        <el-input placeholder="请填写注册邮箱"></el-input>
      </el-form-item>
      <el-form-item label="验证码">
        <el-input
          placeholder="请填写验证码"
          style="width: calc(100% - 140px); margin-right: 20px"
        ></el-input>
        <el-button type="primary" style="width: 120px">发送验证码</el-button>
      </el-form-item>
      <el-form-item label="新密码">
        <el-input placeholder="请输入您的密码" show-password></el-input>
      </el-form-item>
      <el-form-item label="确认密码">
        <el-input placeholder="请确认您的密码" show-password></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="handleClose">取 消</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'Forget',
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    handleClose() {
      this.$refs.form.resetFields()
      this.$emit('close')
    },
    submit() {
      this.handleClose()
    }
  }
}
</script>

<style lang="less" scoped></style>
