<template>
  <el-dialog
    title="注册"
    :visible="visible"
    :before-close="handleClose"
    width="500px"
    top="5vh"
  >
    <el-form ref="form" label-width="80px">
      <el-form-item label="用户类别:">
        <el-select
          style="width: 100%"
          v-model="form.userType"
          placeholder="请选择"
        >
          <el-option label="教师" value="2"></el-option>
          <el-option label="高校学生" value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="用户名">
        <el-input placeholder="请填写注册邮箱"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input placeholder="请输入用户密码" show-password></el-input>
      </el-form-item>
      <el-form-item label="确认密码">
        <el-input placeholder="请确认您的密码" show-password></el-input>
      </el-form-item>
      <el-form-item label="真实姓名">
        <el-input placeholder="请输入姓名"></el-input>
      </el-form-item>
      <el-form-item label="学校/单位">
        <el-input placeholder="请输学校/单位"></el-input>
      </el-form-item>
      <el-form-item label="班级">
        <el-input placeholder="请输班级"></el-input>
      </el-form-item>
      <el-form-item label="地区:">
        <el-select style="width: 49%" placeholder="请选择">
          <el-option label="江苏" value="2"></el-option>
          <el-option label="浙江" value="3"></el-option>
        </el-select>
        <el-select style="width: 49%; margin-left: 2%" placeholder="请选择">
          <!-- <el-option label="教师" value="2"></el-option>
          <el-option label="高校学生" value="3"></el-option> -->
        </el-select>
      </el-form-item>
      <el-form-item label="邮箱">
        <el-input placeholder="请填写注册邮箱"></el-input>
      </el-form-item>
      <el-form-item label="验证码">
        <el-input
          placeholder="请填写验证码"
          style="width: calc(100% - 140px); margin-right: 20px"
        ></el-input>
        <el-button type="primary" style="width: 120px">发送</el-button>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="handleClose">取 消</el-button>
      <el-button type="primary" @click="submit">注 册</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'Register',
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      form: {
        userType: '1'
      }
    }
  },
  methods: {
    handleClose() {
      this.$refs.form.resetFields()
      this.$emit('close')
    },
    submit() {
      this.handleClose()
    }
  }
}
</script>

<style lang="less" scoped></style>
