<template>
  <div class="login">
    <img
      src="http://robweld.zjnu.edu.cn/zsd/img/zsd-logo.775d192e.png"
      class="logo"
    />
    <div class="title">大型钢结构多机器人协同焊接控制虚拟仿真实验</div>
    <div class="login-form">
      <div class="user-type">
        <div @click="seletTab(1)" :class="activeTab == 1 ? 'active' : ''">
          登录入口
        </div>
        <div @click="seletTab(2)" :class="activeTab == 2 ? 'active' : ''">
          专家入口
        </div>
      </div>
      <el-form>
        <el-form-item label="用户类别:">
          <el-select
            style="width: 100%"
            v-model="form.userType"
            placeholder="请选择"
          >
            <el-option label="管理员" value="1"></el-option>
            <el-option label="教师" value="2"></el-option>
            <el-option label="高校学生" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="账号:">
          <el-input prefix-icon="el-icon-mobile-phone"></el-input>
        </el-form-item>
        <el-form-item label="密码:" style="position: relative">
          <el-link
            @click="forgetVisible = true"
            class="forget"
            :underline="false"
            type="primary"
          >
            忘记密码?
          </el-link>
          <el-input prefix-icon="el-icon-lock" show-password></el-input>
        </el-form-item>
      </el-form>
      <div class="register">
        <el-link
          @click="registerVisible = true"
          :underline="false"
          type="primary"
        >
          注册
        </el-link>
      </div>
      <el-button
        @click="login"
        style="width: 100%; margin-top: 15px"
        type="primary"
        >登录</el-button
      >
    </div>
    <!-- 忘记密码 -->
    <forget @close="forgetClose" :visible="forgetVisible"></forget>
    <!-- 注册 -->
    <register @close="registerClose" :visible="registerVisible"></register>
  </div>
</template>

<script>
import Forget from './components/Forget.vue'
import Register from './components/Register.vue'
export default {
  name: 'Login',
  components: {
    Forget,
    Register
  },
  data() {
    return {
      activeTab: 1,
      form: {
        userType: '1'
      },
      forgetVisible: false,
      registerVisible: false
    }
  },
  methods: {
    login() {
      this.$router.replace('/index')
    },
    seletTab(val) {
      this.activeTab = val
    },
    // 忘记密码弹窗关闭
    forgetClose() {
      this.forgetVisible = false
    },
    // 注册弹窗关闭
    registerClose() {
      this.registerVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.login {
  width: 100%;
  height: 100vh;
  background: url('http://robweld.zjnu.edu.cn/zsd/img/login-bg.3ebec5ff.png');
  overflow: hidden;
  background-size: 100% 100%;
  .logo {
    height: 100px;
    display: block;
    margin: 130px auto 0;
  }
  .title {
    text-align: center;
    font-weight: 600;
    color: #fff;
    font-size: 40px;
    margin-top: 15px;
  }
  .login-form {
    box-shadow: 0 34px 24px 0 rgba(0, 0, 0, 0.38),
      0 15px 14px 0 rgba(0, 0, 0, 0.27), 0 2px 4px 0 rgba(0, 0, 0, 0.11);
    width: 400px;
    border-radius: 4px;
    background: #fff;
    margin: 20px auto 0;
    padding: 40px;
    .user-type {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;
      div {
        height: 50px;
        line-height: 50px;
        flex: 1;
        text-align: center;
        font-size: 16px;
        font-weight: bold;
        cursor: pointer;
        border-bottom: 2px solid transparent;
        &.active {
          color: #409eff;
          border-bottom: 2px solid #409eff;
        }
      }
    }
    .forget {
      position: absolute;
      right: 0;
      top: 0;
    }
    .register {
      text-align: right;
    }
  }
}
</style>
